import { base } from "../../../net/proto/proto";
import { BattleBaseComp } from "../BattleBaseComp";
import { battleDataMgr } from "../BattleDataMgr";
import { battleLogicMgr } from "../BattleLogicMgr";
/**
 * 卡牌由卡组最前到手牌末尾
 */
export class Out_PlayerEnergy extends BattleBaseComp<base.IBattleRoomOpOutput_PlayerEnergy> {
    protected start(): void {
        if (battleDataMgr.isPlayerA(this.data.playerId)) {
            battleLogicMgr.energyLabA.string = this.data.energy[this.round - 1].finalValue.toString();
        } else {
            battleLogicMgr.energyLabB.string = this.data.energy[this.round - 1].finalValue.toString();
        }
        
        this.exit();
    }

    protected reset(): void {
        
    }
}